.contact-us {
  box-sizing: border-box;
  min-width: 280px;

  &__content {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;

    @include onTablet {
      display: grid;
    }

    @include onDesktop {
      display: grid;
    }
  }

  &__subtitle {
    @include onDesktop {
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  &__title {
    margin: 0 0 10px;
    text-align: left;

    @include onTablet {
      display: grid;
    }

    @include onDesktop {
      display: grid;
      margin: 0 0 30px;
    }
  }

  &__description {
    margin-bottom: 30px;
    padding: 0;

    @include onTablet {
      margin-bottom: 20px;
    }

    @include onDesktop {
      margin-bottom: 40px;
    }

    &--text {
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: $text-color;

      @include onTablet {
        display: grid;
        font-size: 16px;
        line-height: 24px;
      }

      @include onDesktop {
        display: grid;
      }
    }
  }

  &__field {
    margin: 0 0 25px;

    @include onDesktop {
      margin: 0 0 30px;
    }

    &:nth-child(3) {
      margin: 0 0 45px;

      @include onDesktop {
        margin: 0 0 60px;
      }
    }

    &--last {
      margin-bottom: 30px;
      cursor: default;

      @include onDesktop {
        margin-bottom: 50px;
      }
    }
  }

  &__image {
    display: none;

    @include onTablet {
      display: grid;
      box-sizing: border-box;
      justify-content: stretch;
      min-width: 342px;
      height: auto;
    }

    @include onDesktop {
      min-width: 580px;
    }

    &--photo {
      width: 100%;
      height: auto;
      overflow: auto;
      object-fit: cover;
      min-height: 435px;

      -webkit-transform: scale(1);
      transform: scale(1);
      -webkit-transition: 0.3s ease-in-out;
      transition: 0.3s ease-in-out;

      &:hover {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
      }
    }
  }
}
